/* Base */
html, body, main {
  min-height: 100%;
  height: 100%;

  @include breakpoint-up(480px) {
    overflow-x: hidden;
  }
}

* {
  @include font-roboto;
}

a, a:hover, a:active, a:focus {
  text-decoration: none;
  outline: none;
}


.slick-item.slick-slide {
	background:#fff !important;
}